<template lang="pug">
    .teacher__profile
        .teacher__profile-container
            ul.teacher__profile-list
                li.teacher__profile-item
                    label.label__item Аватар
                    .teacher__profile-photo
                        img(src='../../../assets/images/icons/login_scholar.svg').teacher__photo
                li.teacher__profile-item
                    label.label__item Группы
                    ul.teacher__groups-list
                        li(v-for='item in teacherGroups').teacher__groups-item {{item.code}}
                li.teacher__profile-item
                    label.label__item Программы
                    ul.teacher__programmes-list
                        li(v-for='item in programmes').teacher__programmes-item
                            .teacher__programmes-item-text {{item.name}}
                            button(@click='activateExistedProgramme(item.id)' v-if="item.status==='NOT_ACTIVE'").programme__status-button
                li.teacher__profile-item
                    label.label__item Тесты
                    ul.teacher__tests-list
                        li(v-for='item in teacherTests').teacher__tests-item {{item.code}}
        .buttons__teacher   
            button(@click="removeCurrentTeacher()").remove__teacher-button Удалить данные
</template>    
<script>
import { mapState, mapActions } from "vuex";
export default {  
    data() {
        return {
        }
    },
    props: {
        teacher: Object
    },
    created() {
        this.fetchTeacherTests(this.teacher.id),
        this.fetchTeacherProgrammes(this.teacher.id),
        this.fetchTeacherGroups(this.teacher.id)
    },
    computed: {
        ...mapState ("teacherTests", {
            teacherTests: state => state.teacherTests
        }),
        ...mapState ("programmes", {
            programmes: state => state.programmes
        }),
        ...mapState ("teacherGroups", {
            teacherGroups: state => state.teacherGroups
        })
    },
    methods: {
        ...mapActions("teacherTests", ["fetchTeacherTests"]),
        ...mapActions("programmes", ["fetchTeacherProgrammes", "activateProgramme"]),
        ...mapActions("teachers", ["removeTeacher"]),
        ...mapActions("teacherGroups", ["fetchTeacherGroups"]),
        removeCurrentTeacher() {
            this.removeTeacher(this.teacher.id)
        },
        activateExistedProgramme(data) {
            this.activateProgramme(data)
        }
    }
}
</script>
